<template>
    <div class="l-container">
        <h1>Главная страница</h1>
      <img :src="require('../assets/img/slide1.png')" alt="">
        <CarouselMain :data="SliderMainData"/>

        <Features :data="FeaturesData"/>

        <div class="b-tabs">
            <div class="l-container">
                <h2>Способ получения</h2>
                <p>Вы сами выбираете, куда хотите получить деньги</p>
                <tabs :data="TabsData" v-model="active">
                    <tab :title=" tab.titleTab " v-for="tab in TabsData"
                         :key="tab">
                        <div class="b-tabs__left">
                            <div class="b-tabs__image" :style="{ backgroundImage: 'url(' + tab.image + ')' }"></div>
                        </div>
                        <div class="b-tabs__right">
                            <h2>{{ tab.title }}</h2>
                            <p>{{ tab.teaser }}</p>
                        </div>
                    </tab>
                </tabs>
            </div>
        </div>
    </div>
<!--    <div :class='[sideBarClass]'>Sidebar</div>-->

    <Acc />

</template>

<script>
    import { SliderMainData } from '../data/data.slider-main'
    import { FeaturesData } from '../data/data.features'
    import { TabsData } from '../data/data.tabs'

    import CarouselMain from '../components/CarouselMain'
    import Features from '../components/Features'

    import Tabs from '../components/Tabs'
    import Tab from '../components/Tab'

    import Acc from '../components/Acc'

    import { ref } from 'vue'

    export default {
        name: 'home',
        components: {
            CarouselMain,
            Features,
            Tabs,
            Tab,
            Acc,
        },
        data() {
            return {
                FeaturesData: FeaturesData,
                SliderMainData: SliderMainData,
                TabsData: TabsData,
                sideBarClass: 'desktop-sidebar',
            }
        },
        setup() {
            const active = ref(0);
            return { active };
        },
        mounted() {
          this.handleResize()
          window.addEventListener('resize', this.handleResize)
        },
        unmounted() {
          window.removeEventListener('resize', this.handleResize)
        },
        methods: {
          handleResize() {
            this.sideBarClass = window.matchMedia('(max-width: 600px)').matches ?
                'mobile-sidebar' : 'mobile-sidebar mobile-sidebar_inner asd'
          }
        }
    };
</script>
