<script>
    import { provide, computed, ref } from "vue";

    export default {
        name: "Tabs",
        props: {
            modelValue: {
                type: [String, Number],
            },
        },
        emits: ["update:modelValue"],
        setup(props, { slots, emit }) {/* eslint-disable-line */
            const active = computed(() => props.modelValue);
            const tabs = ref([]);

            function selectTab(tab) {
                emit("update:modelValue", tab);
            }

            provide("tabsState", {
                active,
                tabs,
            });

            return {
                tabs,
                active,
                selectTab,
            };
        },
    };
</script>

<template>
    <ul class="b-tabs__nav">
        <li
                v-for="(tab, i) of tabs"
                :key="i"
                :class="
                active === i
                    ? 'b-tabs__item_active'
                    : 'b-tabs__item'
            "
                class=""
                @click="selectTab(i)"
        >
            {{ tab.props.title }}
        </li>
    </ul>
    <div class="b-tabs__ctn">
        <slot />
    </div>
</template>
